body {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 0.7em;
  margin: 0;
  padding: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.52)), to(rgba(0, 0, 0, 0.73))), url("background.jpg");
  background-image: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.52), rgba(0, 0, 0, 0.73)), url("background.jpg");
  background-image: -o-linear-gradient(top, rgba(0, 0, 0, 0.52), rgba(0, 0, 0, 0.73)), url("background.jpg");
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.52), rgba(0, 0, 0, 0.73)), url("background.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  color: #fff;
}

.App {
  text-align: center;
}

.App li {
  list-style: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 4em;
}

.App-intro {
  font-size: large;
}

.balance {
  font-size: 3em;
}

.balance-number {
  font-size: 6em;
}

.title {
  background: rgba(0, 0, 0, 0.4);
  padding: 20px;
  font-size: 6em;
}

.entry-form {
  background: rgba(0, 0, 0, 0.7);
  padding: 20px;
}

#income {
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.7);
}

#expense {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.7);
}

#income,
#expense {
  padding: 30px 0px 50px;
}

span {
  margin: 20px;
}

input,
button {
  margin: 10px;
}

#lists {
  display: grid;
  min-height: 200px;
  grid-gap: 10px;
  margin: 20px;
  grid-template-columns: 1fr 1fr;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width: 1024px) {
  #lists {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 768px) {
  #lists {
    grid-template-columns: 1fr;
  }
}


